@import '../../../base-styles/mixins.scss';
@import '../../../base-styles/variables.scss';

.about {
  background-color: $color-grey--light;
  padding: 10rem 0;
  margin: 4rem 0;

  &__container {
    @include main-block;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include max500 {
      @include list;
      // flex-direction: column-reverse;
    }
    &__img {
      width: 46%;

      @include max500 {
        width: 80%;
        height: 30rem;
      }
      @include max300 {
        width: 90%;
        height: 26rem;
      }
      & img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &__text {
      width: 50%;
      max-width: 45rem;
      @include max500 {
        width: 80%;
      }
      @include max300 {
        width: 100%;
      }

      h3 {
        margin-bottom: 2rem;
      }
    }
  }
}
